<template>
  <v-app>
    <Toolbar />
    <Sidebar />
    <v-content>
      <div class="loading-content" v-if="$store.state.loading">
        <v-progress-circular
          indeterminate
          color="primary"
        ></v-progress-circular>
      </div>

      <router-view />
    </v-content>
    <Footer />
  </v-app>
</template>

<script>
import LocalStorage from "@/utils/helpers/localStorage";

export default {
  components: {
    Toolbar: () => import("@/components/restricted/commons/Toolbar"),
    Sidebar: () => import("@/components/restricted/commons/Sidebar"),
    Footer: () => import("@/components/restricted/commons/Footer"),
  },

  beforeCreate() {
    if (!LocalStorage.get("authToken")) {
      this.$router.push("/restricted/login");
    }
  },
};
</script>

